/**
 * 不同角色对应不同路由 存储的是角色对应的权限名称
 */
const roleToRoute = {
  // 普通用户
  coustomer: [
    { name: 'Product' },
    { name: 'ProductList' },
    { name: 'ProductAdd' },
    { name: 'ProductEdit' },
  ],
  // 管理员
  admin: [
    { name: 'Product' },
    { name: 'ProductList' },
    { name: 'ProductAdd' },
    { name: 'Category' },
    { name: 'ProductEdit' },
  ],
};
// 通过角色判断有哪些路由
/**
 * 过滤掉没有权限的路由
 * @param {Array} routes 所有路由
 * @param {String} role 角色
 * 返回角色对应的路由
 */
export default function getMenuRoute(routes, role) {
  // console.log(routes, role);
  // 得到角色拥有权限的所对应的路由名称
  const allowRoutesName = roleToRoute[role].map((item) => item.name);
  const resultRoutes = routes.filter((r) => {
    const obj = r;
    if (allowRoutesName.indexOf(r.name) !== -1) {
      const { children } = obj;
      obj.children = children.filter((c) => allowRoutesName.indexOf(c.name) !== -1);
      return true;
    }
    return false;
  });
  return resultRoutes;
}
