import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '@/store';
import Home from '../views/layout/Home.vue';
import Login from '../views/layout/login.vue';
// import Index from '../views/page/Index.vue';
import getMenuRoute from '../utils/permission';

Vue.use(VueRouter);
const asyncRouterMap = [{
  path: '/product',
  name: 'Product',
  meta: {
    title: '商品',
    icon: 'inbox',
    hidden: false,
  },
  component: Home,

  children: [{
    path: 'list',
    name: 'ProductList',
    meta: {
      title: '商品列表',
      icon: 'unordered-list',
      hidden: false,

    },
    component: () => import('../views/page/ProductList.vue'),
  }, {
    path: 'add',
    name: 'ProductAdd',
    meta: {
      title: '新增商品',
      icon: 'file-add',
      hidden: false,
    },
    component: () => import('../views/page/ProductAdd.vue'),
  },
  {
    path: 'edit/:id',
    name: 'ProductEdit',
    meta: {
      title: '编辑商品',
      icon: 'file-add',
      hidden: true,
    },
    component: () => import('../views/page/ProductAdd.vue'),
  },
  {
    path: 'category',
    name: 'Category',
    meta: {
      title: '类目管理',
      hidden: false,
      icon: 'project',
    },
    component: () => import('../views/page/Category.vue'),
  },
  ],
}];
const routes = [{
  path: '/',
  name: 'Home',
  component: Home,
  redirect: 'index',
  meta: {
    title: '首页',
    icon: 'home',
    hidden: false,
  },

  children: [{
    path: 'index',
    name: 'Index',
    // component: Index,
    meta: {
      title: '统计',
      hidden: false,
      icon: 'number',
    },
    component: () => import('../views/page/Index.vue'),
  }],

},
{
  path: '/login',
  name: 'Path',
  meta: {
    title: '登录',
    hidden: true,

  },
  component: Login,
},
];

const router = new VueRouter({
  routes,
});

export default router;
let isAdd = false;
router.beforeEach((to, from, next) => {
  if (to.path !== '/login') {
    // 判断目标路由路径是否不是登录页面 如果不是则判断store中是否有以下信息
    if (store.state.user.appkey && store.state.user.username && store.state.user.role) {
      if (!isAdd) {
        // 进入系统后判断当前用户角色对应有哪些路由权限
        const menuRoutes = getMenuRoute(asyncRouterMap, store.state.user.role);
        // console.log(menuRoutes);
        store.dispatch('changeMenuRoutes', routes.concat(menuRoutes)).then(() => {
          // 添加路由时由于是在beforeEach里，所以每次进入路由都会添加而导致栈溢出 解决方法：isAdd
          router.addRoutes(menuRoutes);
          next();
        });
        isAdd = true;
      }
      next(); // 有这些信息则继续执行
    } else {
      next('/login'); // 没有这些信息则跳转到登录页面
    }
  }
  next();
});
